@tailwind base;
@tailwind components;
@tailwind utilities;

html {
  scroll-behavior: smooth;
}

.font-maven {
  font-family: "Maven Pro", sans-serif;
}

button {
  @apply rounded py-3 px-4 bg-green-400 text-blue-900 font-bold shadow-md leading-none;
}

button:hover {
  @apply bg-green-300 text-green-800;
}

button:focus {
  @apply outline-none shadow-outline;
}

button[disabled] {
  @apply bg-green-200 text-green-600 cursor-not-allowed;
}

pre {
  @apply font-mono text-gray-800 bg-gray-200 p-4 rounded-lg shadow-inner;
}

.card {
  @apply rounded-lg overflow-hidden shadow-lg bg-white px-6 py-4 border border-gray-200;
}

.card > h2:first-child {
  @apply font-semibold text-inter-xl text-gray-700 flex;
}

.card > .live-preview {
  @apply flex flex-col justify-center items-center mt-6;
}

.card > .live-preview strong {
  @apply text-inter-xl font-bold mb-6;
}

.card li {
  @apply block px-4 py-2 text-sm leading-5 text-gray-700;
}

.card > li:hover {
  @apply bg-gray-100 text-gray-900;
}

.card > li:focus {
  @apply outline-none bg-gray-100 text-gray-900;
}

.anchor {
  @apply font-light text-gray-300 transition-colors duration-200 ease-in-out;
}

.anchor:hover {
  @apply text-gray-500;
}

header h1 span {
  @apply inline-block;
  animation: wooosh 1s alternate ease-in-out infinite;
  animation-play-state: paused;
}

.wooosh span {
  animation-play-state: running;
}

.wooosh > div > span:nth-of-type(2) {
  animation-delay: 0.1s;
}
.wooosh > div > span:nth-of-type(3) {
  animation-delay: 0.2s;
}
.wooosh > div > span:nth-of-type(4) {
  animation-delay: 0.3s;
}
.wooosh > div > span:nth-of-type(5) {
  animation-delay: 0.4s;
}
.wooosh > div > span:nth-of-type(6) {
  animation-delay: 0.5s;
}
.wooosh > div > span:nth-of-type(7) {
  animation-delay: 0.6s;
}
.wooosh > div > span:nth-of-type(8) {
  animation-delay: 0.7s;
}

@keyframes wooosh {
  to {
    @apply opacity-50 text-yellow-500;
    transform: scale(1.5, 1.5);
  }
}
